.App {
  min-height: 100vh;
  background: linear-gradient(147.52deg, #F9FAFB 8.89%, #D2D6DB 100.48%);
  
  padding: 16px;
}

.main-container {
  /* Background/primary */
  background: #FFFFFF;
  /* shadow / shadow-lg */
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1), 0px 4px 6px -2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;

  width: 100%;
  height: 100%;
}

